:root {
  --text--white: #fff;
  --hero-background: #f24c26;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  text-align: center;
  z-index: 1000; /* Ensure it stays on top */
}

.nav-component ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

.nav-component ul li {
  padding: 10px;
  font-size: 1.2rem;
}

.nav-component ul li a {
  text-decoration: none;
  color: var(--text--white);
}

.nav-end {
  display: flex;
}

.hero-component {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--text--white);
  background-color: var(--hero-background);
}

.hero-component h1 {
  font-size: 3rem;
}

.quote-component {
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  height: 100vh;
  color: var(--text--white);
  background-color: #1E01AD;
  padding: 0 0.5rem;
}

.quote-component span {
  font-size: 2.7rem;
  font-weight: bold;
}

.quote-component span:nth-child(2){
  padding-left: 4rem;
}

.quote-component span:nth-child(3){
  padding-left: 8rem;
}
